import { AnalyticsEvent } from '../types/widget.types';

import { mockApiService } from '../services/mockApiService';

// Analytics service for tracking widget events
class AnalyticsService {
  private apiUrl: string = '';
  private enabled: boolean = true;
  private queue: AnalyticsEvent[] = [];
  private isOnline: boolean = navigator.onLine;

  constructor() {
    // Listen for online/offline events
    window.addEventListener('online', () => {
      this.isOnline = true;
      this.flushQueue();
    });
    
    window.addEventListener('offline', () => {
      this.isOnline = false;
    });
  }

  // Initialize analytics with configuration
  init(apiUrl: string, enabled: boolean = true) {
    this.apiUrl = apiUrl;
    this.enabled = enabled;
  }

  // Send analytics event
  async send(event: AnalyticsEvent): Promise<void> {
    if (!this.enabled) return;

    // Add to queue if offline
    if (!this.isOnline) {
      this.queue.push(event);
      return;
    }

    try {
      await this.sendEvent(event);
    } catch (error) {
      console.error('Analytics error:', error);
      // Add to queue for retry
      this.queue.push(event);
    }
  }

  // Send event to analytics endpoint
  private async sendEvent(event: AnalyticsEvent): Promise<void> {
    // Use mock service instead of real network request
    await mockApiService.sendAnalyticsEvent({
      ...event,
      // Add client-side metadata
      client: {
        userAgent: navigator.userAgent,
        language: navigator.language,
        timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
        screenResolution: `${screen.width}x${screen.height}`,
        windowSize: `${window.innerWidth}x${window.innerHeight}`,
        url: window.location.href,
        referrer: document.referrer,
      },
    });
  }

  // Flush queued events
  private async flushQueue(): Promise<void> {
    if (this.queue.length === 0) return;

    const events = [...this.queue];
    this.queue = [];

    try {
      // Send events in batch using mock service
      for (const event of events) {
        await mockApiService.sendAnalyticsEvent(event);
      }
    } catch (error) {
      console.error('Batch analytics error:', error);
      // Re-add failed events to queue
      this.queue.unshift(...events);
    }
  }

  // Track page view
  trackPageView(chatbotId: string, tenantId: string): void {
    this.send({
      type: 'page_view',
      sessionId: this.generateSessionId(),
      chatbotId,
      tenantId,
      timestamp: new Date(),
      metadata: {
        url: window.location.href,
        referrer: document.referrer,
        title: document.title,
      },
    });
  }

  // Track widget load
  trackWidgetLoad(chatbotId: string, tenantId: string, loadTime: number): void {
    this.send({
      type: 'widget_load',
      sessionId: this.generateSessionId(),
      chatbotId,
      tenantId,
      timestamp: new Date(),
      metadata: {
        loadTime,
        url: window.location.href,
      },
    });
  }

  // Track user engagement
  trackEngagement(chatbotId: string, tenantId: string, sessionId: string, action: string): void {
    this.send({
      type: 'user_engagement',
      sessionId,
      chatbotId,
      tenantId,
      timestamp: new Date(),
      metadata: {
        action,
        url: window.location.href,
      },
    });
  }

  // Track conversion events
  trackConversion(chatbotId: string, tenantId: string, sessionId: string, conversionType: string, value?: number): void {
    this.send({
      type: 'conversion',
      sessionId,
      chatbotId,
      tenantId,
      timestamp: new Date(),
      metadata: {
        conversionType,
        value,
        url: window.location.href,
      },
    });
  }

  // Generate session ID
  private generateSessionId(): string {
    return `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  }
}

// Global analytics instance
const analytics = new AnalyticsService();

// Export convenience function
export const sendAnalyticsEvent = (event: AnalyticsEvent): void => {
  analytics.send(event);
};

// Export analytics service
export { analytics };

// Helper functions for common tracking scenarios
export const trackChatStart = (chatbotId: string, tenantId: string, sessionId: string): void => {
  sendAnalyticsEvent({
    type: 'chat_started',
    sessionId,
    chatbotId,
    tenantId,
    timestamp: new Date(),
  });
};

