import { useState, useEffect, useCallback, useRef } from 'react';
import { ChatbotConfig, ChatSession, Message, ConversationalForm } from '../types/widget.types';
import { mockApiService } from '../services/mockApiService';
import { sendAnalyticsEvent } from '../utils/analytics';

export interface ChatState {
  isOpen: boolean;
  session: ChatSession | null;
  formContext: ConversationalForm | null;
  isTyping: boolean;
  unreadCount: number;
  connectionStatus: 'connected' | 'connecting' | 'disconnected';
  error: string | null;
}

export interface ChatActions {
  toggleChat: () => void;
  minimizeChat: () => void;
  maximizeChat: () => void;
  addUserMessage: (content: string) => Promise<void>;
  escalateToAgent: () => Promise<void>;
  startTyping: () => void;
  stopTyping: () => void;
  clearError: () => void;
  startForm: (formId: string) => Promise<void>;
  submitFormField: (fieldId: string, value: any) => Promise<void>;
  completeForm: (data: Record<string, any>) => Promise<void>;
  cancelForm: () => void;
}

export const useChat = (config: ChatbotConfig | null) => {
  const [state, setState] = useState<ChatState>({
    isOpen: false,
    session: null,
    formContext: null,
    isTyping: false,
    unreadCount: 0,
    connectionStatus: 'disconnected',
    error: null,
  });

  const typingTimeoutRef = useRef<NodeJS.Timeout>();
  const sessionRef = useRef<ChatSession | null>(null);

  // Initialize chat session
  useEffect(() => {
    if (config && !sessionRef.current) {
      initializeSession();
    }
  }, [config]);

  const initializeSession = async () => {
    if (!config) return;

    try {
      setState(prev => ({ ...prev, connectionStatus: 'connecting' }));
      
      const session = await mockApiService.createChatSession(config.id);
      sessionRef.current = session;
      
      setState(prev => ({
        ...prev,
        session,
        connectionStatus: 'connected',
        error: null,
      }));

      // Send welcome message
      if (config.welcomeMessage) {
        setTimeout(() => {
          addBotMessage(config.welcomeMessage);
        }, 1000);
      }

      // Track session start
      sendAnalyticsEvent({
        type: 'chat_session_started',
        sessionId: session.id,
        chatbotId: config.id,
        tenantId: config.tenantId,
        timestamp: new Date(),
        metadata: {},
      });

    } catch (error) {
      console.error('Failed to initialize chat session:', error);
      setState(prev => ({
        ...prev,
        connectionStatus: 'disconnected',
        error: 'Failed to connect to chat service',
      }));
    }
  };

  const addBotMessage = useCallback((content: string, options?: any) => {
    if (!sessionRef.current) return;

    const message: Message = {
      id: `msg_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      type: 'bot',
      content,
      timestamp: new Date(),
      options,
    };

    sessionRef.current.messages.push(message);
    
    setState(prev => ({
      ...prev,
      session: { ...sessionRef.current! },
      unreadCount: prev.isOpen ? prev.unreadCount : prev.unreadCount + 1,
      isTyping: false,
    }));

    // Track bot message
    sendAnalyticsEvent({
      type: 'message_received',
      sessionId: sessionRef.current.id,
      chatbotId: config?.id || '',
      tenantId: config?.tenantId || '',
      timestamp: new Date(),
      metadata: { messageType: 'bot', hasOptions: !!options },
    });
  }, [config]);

  const addUserMessage = useCallback(async (content: string) => {
    if (!sessionRef.current || !config) return;

    const userMessage: Message = {
      id: `msg_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      type: 'user',
      content,
      timestamp: new Date(),
    };

    sessionRef.current.messages.push(userMessage);
    
    setState(prev => ({
      ...prev,
      session: { ...sessionRef.current! },
    }));

    // Track user message
    sendAnalyticsEvent({
      type: 'message_sent',
      sessionId: sessionRef.current.id,
      chatbotId: config.id,
      tenantId: config.tenantId,
      timestamp: new Date(),
      metadata: { messageType: 'user' },
    });

    try {
      // Get AI response
      const response = await mockApiService.sendMessage(sessionRef.current.id, content);
      
      // Simulate typing delay
      setState(prev => ({ ...prev, isTyping: true }));
      
      setTimeout(() => {
        addBotMessage(response.content, response.options);
        setState(prev => ({ ...prev, isTyping: false }));
      }, 1500);

    } catch (error) {
      console.error('Failed to send message:', error);
      setTimeout(() => {
        addBotMessage('Sorry, I encountered an error. Please try again.');
        setState(prev => ({ ...prev, isTyping: false }));
      }, 1000);
    }
  }, [config, addBotMessage]);

  const toggleChat = useCallback(() => {
    setState(prev => {
      const newIsOpen = !prev.isOpen;
      
      if (newIsOpen) {
        // Track chat opened
        sendAnalyticsEvent({
          type: 'chat_opened',
          sessionId: prev.session?.id || '',
          chatbotId: config?.id || '',
          tenantId: config?.tenantId || '',
          timestamp: new Date(),
          metadata: {},
        });
        
        return {
          ...prev,
          isOpen: true,
          unreadCount: 0,
        };
      } else {
        // Track chat closed
        sendAnalyticsEvent({
          type: 'chat_closed',
          sessionId: prev.session?.id || '',
          chatbotId: config?.id || '',
          tenantId: config?.tenantId || '',
          timestamp: new Date(),
          metadata: {},
        });
        
        return {
          ...prev,
          isOpen: false,
        };
      }
    });
  }, [config]);

  const minimizeChat = useCallback(() => {
    setState(prev => ({ ...prev, isOpen: false }));
  }, []);

  const maximizeChat = useCallback(() => {
    setState(prev => ({ ...prev, isOpen: true, unreadCount: 0 }));
  }, []);

  const escalateToAgent = useCallback(async () => {
    if (!sessionRef.current || !config) return;

    try {
      setState(prev => ({ ...prev, isTyping: true }));
      
      await mockApiService.escalateToAgent(sessionRef.current.id);
      
      setTimeout(() => {
        addBotMessage(
          "I'm connecting you with a human agent. Please wait a moment...",
          {
            type: 'escalation',
            status: 'connecting'
          }
        );
      }, 1000);

      // Simulate agent joining
      setTimeout(() => {
        addBotMessage(
          "Hi! I'm Sarah, a human agent. How can I help you today?",
          {
            type: 'agent_joined',
            agentName: 'Sarah',
            agentAvatar: 'https://images.pexels.com/photos/774909/pexels-photo-774909.jpeg?auto=compress&cs=tinysrgb&w=64&h=64&dpr=2'
          }
        );
      }, 3000);

      // Track escalation
      sendAnalyticsEvent({
        type: 'escalated_to_agent',
        sessionId: sessionRef.current.id,
        chatbotId: config.id,
        tenantId: config.tenantId,
        timestamp: new Date(),
        metadata: {},
      });

    } catch (error) {
      console.error('Failed to escalate to agent:', error);
      setTimeout(() => {
        addBotMessage('Sorry, no agents are available right now. Please try again later.');
      }, 1000);
    }
  }, [config, addBotMessage]);

  const startTyping = useCallback(() => {
    setState(prev => ({ ...prev, isTyping: true }));
    
    // Clear existing timeout
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current);
    }
    
    // Auto-stop typing after 5 seconds
    typingTimeoutRef.current = setTimeout(() => {
      setState(prev => ({ ...prev, isTyping: false }));
    }, 5000);
  }, []);

  const stopTyping = useCallback(() => {
    setState(prev => ({ ...prev, isTyping: false }));
    
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current);
    }
  }, []);

  const clearError = useCallback(() => {
    setState(prev => ({ ...prev, error: null }));
  }, []);

  const startForm = useCallback(async (formId: string) => {
    if (!config) return;

    try {
      const form = await mockApiService.getForm(formId);
      
      const formContext: ConversationalForm = {
        formId: form.id,
        form,
        currentFieldIndex: 0,
        collectedData: {},
        validationErrors: {},
        isComplete: false,
      };

      setState(prev => ({ ...prev, formContext }));

      // Start form with welcome message
      setTimeout(() => {
        addBotMessage(
          `Great! I'll help you with the ${form.name}. ${form.description}`,
          {
            type: 'form_start',
            formId: form.id,
            formName: form.name,
          }
        );
      }, 500);

      // Present first field
      setTimeout(() => {
        presentFormField(form.fields[0], formContext);
      }, 1500);

      // Track form start
      sendAnalyticsEvent({
        type: 'form_started',
        sessionId: sessionRef.current?.id || '',
        chatbotId: config.id,
        tenantId: config.tenantId,
        timestamp: new Date(),
        metadata: { formId: form.id, formName: form.name },
      });

    } catch (error) {
      console.error('Failed to start form:', error);
      addBotMessage('Sorry, I couldn\'t load the form. Please try again.');
    }
  }, [config, addBotMessage]);

  const presentFormField = useCallback((field: any, context: ConversationalForm) => {
    const fieldPrompts: Record<string, (field: any) => string> = {
      text: (field) => `What's your ${field.label.toLowerCase()}?`,
      email: (field) => `I'll need your ${field.label.toLowerCase()}. What email should I use?`,
      number: (field) => `How many ${field.label.toLowerCase()} would you like?`,
      select: (field) => `Please choose your ${field.label.toLowerCase()}:`,
      textarea: (field) => `Could you tell me more about ${field.label.toLowerCase()}?`,
      file: (field) => `Please upload your ${field.label.toLowerCase()}. I can process most document types.`,
      checkbox: (field) => `Do you ${field.label.toLowerCase()}?`,
      date: (field) => `When would you prefer for ${field.label.toLowerCase()}?`,
    };

    const prompt = fieldPrompts[field.type]?.(field) || `Please provide your ${field.label.toLowerCase()}:`;
    
    const options: any = {
      type: 'form_field',
      fieldId: field.id,
      fieldType: field.type,
      required: field.required,
    };

    if (field.type === 'select' && field.options) {
      options.quickReplies = field.options.map((option: string) => ({
        text: option,
        value: option,
      }));
    }

    if (field.type === 'checkbox') {
      options.quickReplies = [
        { text: 'Yes', value: true },
        { text: 'No', value: false },
      ];
    }

    addBotMessage(prompt, options);
  }, [addBotMessage]);

  const submitFormField = useCallback(async (fieldId: string, value: any) => {
    if (!state.formContext || !config) return;

    const currentField = state.formContext.form?.fields[state.formContext.currentFieldIndex];
    if (!currentField || currentField.id !== fieldId) return;

    try {
      // Validate field
      const isValid = await mockApiService.validateFormField(fieldId, value);
      
      if (!isValid.success) {
        addBotMessage(`${isValid.message} Please try again.`);
        return;
      }

      // Store field data
      const updatedContext = {
        ...state.formContext,
        collectedData: {
          ...state.formContext.collectedData,
          [fieldId]: value,
        },
        currentFieldIndex: state.formContext.currentFieldIndex + 1,
      };

      // Confirmation message
      const confirmationMessages: Record<string, (value: any) => string> = {
        text: (value) => `Got it! I have "${value}".`,
        email: (value) => `Perfect! I'll use ${value}.`,
        number: (value) => `Great! I have ${value}.`,
        select: (value) => `Excellent choice! I've noted "${value}".`,
        checkbox: (value) => value ? 'Understood!' : 'Okay, noted.',
        file: (value) => `Thanks! I've received your ${value}.`,
      };

      const confirmation = confirmationMessages[currentField.type]?.(value) || 'Thank you!';
      addBotMessage(confirmation);

      // Check if form is complete
      if (updatedContext.currentFieldIndex >= (state.formContext.form?.fields.length || 0)) {
        updatedContext.isComplete = true;
        setState(prev => ({ ...prev, formContext: updatedContext }));
        
        // Show form summary
        setTimeout(() => {
          showFormSummary(updatedContext);
        }, 1000);
      } else {
        setState(prev => ({ ...prev, formContext: updatedContext }));
        
        // Present next field
        setTimeout(() => {
          const nextField = state.formContext?.form?.fields[updatedContext.currentFieldIndex];
          if (nextField) {
            presentFormField(nextField, updatedContext);
          }
        }, 1000);
      }

      // Track field completion
      sendAnalyticsEvent({
        type: 'form_field_completed',
        sessionId: sessionRef.current?.id || '',
        chatbotId: config.id,
        tenantId: config.tenantId,
        timestamp: new Date(),
        metadata: {
          fieldId,
          fieldType: currentField.type,
          formId: state.formContext.formId,
        },
      });

    } catch (error) {
      console.error('Failed to submit form field:', error);
      addBotMessage('Sorry, there was an error processing your response. Please try again.');
    }
  }, [state.formContext, config, addBotMessage, presentFormField]);

  const showFormSummary = useCallback((context: ConversationalForm) => {
    if (!context.form) return;

    let summary = "Here's what I collected:\n\n";
    
    Object.entries(context.collectedData).forEach(([fieldId, value]) => {
      const field = context.form?.fields.find(f => f.id === fieldId);
      if (field) {
        const displayValue = typeof value === 'boolean' ? (value ? 'Yes' : 'No') : value;
        summary += `• ${field.label}: ${displayValue}\n`;
      }
    });

    summary += "\nWould you like to submit this information?";

    addBotMessage(summary, {
      type: 'form_summary',
      quickReplies: [
        { text: 'Submit', value: 'submit' },
        { text: 'Edit', value: 'edit' },
        { text: 'Cancel', value: 'cancel' },
      ],
    });
  }, [addBotMessage]);

  const completeForm = useCallback(async (data: Record<string, any>) => {
    if (!state.formContext || !config) return;

    try {
      const result = await mockApiService.submitForm(state.formContext.formId, data);
      
      if (result.success) {
        addBotMessage('Perfect! Your information has been submitted successfully. Is there anything else I can help you with?');
        
        // Track form completion
        sendAnalyticsEvent({
          type: 'form_completed',
          sessionId: sessionRef.current?.id || '',
          chatbotId: config.id,
          tenantId: config.tenantId,
          timestamp: new Date(),
          metadata: {
            formId: state.formContext.formId,
            fieldCount: Object.keys(data).length,
          },
        });
      } else {
        addBotMessage('I encountered an issue submitting your form. Let me try again or you can contact support.');
      }

      // Clear form context
      setState(prev => ({ ...prev, formContext: null }));

    } catch (error) {
      console.error('Failed to complete form:', error);
      addBotMessage('Sorry, there was an error submitting your form. Please try again.');
    }
  }, [state.formContext, config, addBotMessage]);

  const cancelForm = useCallback(() => {
    setState(prev => ({ ...prev, formContext: null }));
    addBotMessage('No problem! Your information hasn\'t been submitted. How else can I help you?');
  }, [addBotMessage]);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      if (typingTimeoutRef.current) {
        clearTimeout(typingTimeoutRef.current);
      }
    };
  }, []);

  const actions: ChatActions = {
    toggleChat,
    minimizeChat,
    maximizeChat,
    addUserMessage,
    escalateToAgent,
    startTyping,
    stopTyping,
    clearError,
    startForm,
    submitFormField,
    completeForm,
    cancelForm,
  };

  return { state, actions };
};