import {
  mockChatbotConfigs,
  mockForms,
  mockApiResponses,
  mockValidationResponses,
  mockOCRResponses,
  addMockAnalyticsEvent,
  simulateTypingDelay,
  generateSessionId,
} from '../data/mockData';
import {
  ChatbotConfig,
  AIForm,
  Message,
  ChatSession,
  ConversationalForm,
} from '../types/widget.types';

// Mock API service that simulates backend functionality
class MockApiService {
  private sessions: Map<string, ChatSession> = new Map();
  private formContexts: Map<string, ConversationalForm> = new Map();

  // Configuration endpoints
  async getChatbotConfig(chatbotId: string): Promise<ChatbotConfig> {
    await this.simulateNetworkDelay();

    const config = mockChatbotConfigs[chatbotId];
    if (!config) {
      throw new Error(`Chatbot configuration not found for ID: ${chatbotId}`);
    }

    return { ...config };
  }

  // Chat session endpoints
  async createChatSession(chatbotId: string): Promise<ChatSession> {
    await this.simulateNetworkDelay();

    const session = mockApiResponses.createSession(chatbotId) as ChatSession;
    this.sessions.set(session.id, session);

    // Track session creation
    addMockAnalyticsEvent({
      type: 'chat_started',
      sessionId: session.id,
      chatbotId,
      tenantId: mockChatbotConfigs[chatbotId]?.tenantId || 'default',
    });

    return session;
  }

  async sendMessage(sessionId: string, content: string): Promise<Message> {
    await this.simulateNetworkDelay();
    await simulateTypingDelay(); // Simulate bot thinking time

    const session = this.sessions.get(sessionId);
    if (!session) {
      throw new Error(`Session not found: ${sessionId}`);
    }

    // Create user message
    const userMessage: Message = {
      id: Date.now().toString(),
      type: 'user',
      content,
      timestamp: new Date(),
    };

    // Add user message to session
    session.messages.push(userMessage);

    // Generate bot response
    const botResponse = mockApiResponses.sendMessage(sessionId, content);
    session.messages.push(botResponse);

    // Update session metadata
    session.metadata.lastActivity = new Date();

    // Track message events
    addMockAnalyticsEvent({
      type: 'message_sent',
      sessionId,
      chatbotId: session.chatbotId,
      tenantId: mockChatbotConfigs[session.chatbotId]?.tenantId || 'default',
      metadata: { messageLength: content.length },
    });

    addMockAnalyticsEvent({
      type: 'message_received',
      sessionId,
      chatbotId: session.chatbotId,
      tenantId: mockChatbotConfigs[session.chatbotId]?.tenantId || 'default',
      metadata: { messageLength: botResponse.content.length },
    });

    return botResponse;
  }

  async getMessageHistory(sessionId: string): Promise<Message[]> {
    await this.simulateNetworkDelay();

    const session = this.sessions.get(sessionId);
    if (!session) {
      throw new Error(`Session not found: ${sessionId}`);
    }

    return [...session.messages];
  }

  async escalateToAgent(sessionId: string): Promise<void> {
    await this.simulateNetworkDelay();

    const session = this.sessions.get(sessionId);
    if (!session) {
      throw new Error(`Session not found: ${sessionId}`);
    }

    // Update session status
    session.status = 'escalated';

    // Add system message
    const systemMessage: Message = {
      id: Date.now().toString(),
      type: 'system',
      content:
        'Your conversation has been escalated to a human agent. Please wait while we connect you...',
      timestamp: new Date(),
    };
    session.messages.push(systemMessage);

    // Simulate agent joining after delay
    setTimeout(() => {
      const agentMessage: Message = {
        id: Date.now().toString(),
        type: 'agent',
        content:
          "Hello! I'm Sarah, a human agent. I've reviewed your conversation and I'm here to help. How can I assist you further?",
        timestamp: new Date(),
      };
      session.messages.push(agentMessage);
    }, 3000);

    // Track escalation
    addMockAnalyticsEvent({
      type: 'escalation_requested',
      sessionId,
      chatbotId: session.chatbotId,
      tenantId: mockChatbotConfigs[session.chatbotId]?.tenantId || 'default',
    });
  }

  // Form endpoints
  async getForm(formId: string): Promise<AIForm> {
    await this.simulateNetworkDelay();

    const form = mockForms[formId];
    if (!form) {
      throw new Error(`Form not found: ${formId}`);
    }

    return { ...form };
  }

  async startForm(
    sessionId: string,
    formId: string,
  ): Promise<ConversationalForm> {
    await this.simulateNetworkDelay();

    const form = await this.getForm(formId);
    const formContext: ConversationalForm = {
      formId,
      form,
      currentFieldIndex: 0,
      collectedData: {},
      validationErrors: {},
      isComplete: false,
    };

    this.formContexts.set(sessionId, formContext);

    // Track form start
    addMockAnalyticsEvent({
      type: 'form_started',
      sessionId,
      chatbotId: 'demo-chatbot', // Would be dynamic in real implementation
      tenantId: form.tenantId,
      metadata: { formId },
    });

    return formContext;
  }

  async submitFormField(
    sessionId: string,
    fieldId: string,
    value: any,
  ): Promise<{ isValid: boolean; message?: string }> {
    await this.simulateNetworkDelay();

    const formContext = this.formContexts.get(sessionId);
    if (!formContext || !formContext.form) {
      throw new Error(`Form context not found for session: ${sessionId}`);
    }

    const field = formContext.form.fields.find((f) => f.id === fieldId);
    if (!field) {
      throw new Error(`Field not found: ${fieldId}`);
    }

    // Validate field
    const validation = this.validateFormField(field.type, value);
    if (!validation.isValid) {
      formContext.validationErrors[fieldId] =
        validation.message || 'Invalid input';
      return validation;
    }

    // Store valid data
    formContext.collectedData[fieldId] = value;
    delete formContext.validationErrors[fieldId];

    // Move to next field
    formContext.currentFieldIndex++;

    // Check if form is complete
    if (formContext.currentFieldIndex >= formContext.form.fields.length) {
      formContext.isComplete = true;
    }

    return { isValid: true };
  }

  async submitForm(
    sessionId: string,
  ): Promise<{ success: boolean; submissionId?: string }> {
    await this.simulateNetworkDelay();

    const formContext = this.formContexts.get(sessionId);
    if (!formContext || !formContext.form) {
      throw new Error(`Form context not found for session: ${sessionId}`);
    }

    if (!formContext.isComplete) {
      throw new Error('Form is not complete');
    }

    const submissionId = `submission_${Date.now()}`;
    formContext.submissionId = submissionId;

    // Track form completion
    addMockAnalyticsEvent({
      type: 'form_completed',
      sessionId,
      chatbotId: 'demo-chatbot',
      tenantId: formContext.form.tenantId,
      metadata: {
        formId: formContext.formId,
        submissionId,
        fieldCount: formContext.form.fields.length,
      },
    });

    // Clean up form context
    this.formContexts.delete(sessionId);

    return { success: true, submissionId };
  }

  // AI-enhanced validation
  async smartValidateField(
    fieldType: string,
    value: any,
  ): Promise<{ isValid: boolean; suggestion?: string; confidence: number }> {
    await this.simulateNetworkDelay(200); // Shorter delay for validation

    if (fieldType === 'email') {
      const emailValidation = mockValidationResponses.email[value as string];
      if (emailValidation) {
        return emailValidation;
      }

      // Default email validation
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      const isValid = emailRegex.test(value);
      return {
        isValid,
        suggestion: isValid ? undefined : 'Please enter a valid email address',
        confidence: 0.9,
      };
    }

    if (fieldType === 'text') {
      const nameValidation = mockValidationResponses.name[value as string];
      if (nameValidation) {
        return nameValidation;
      }

      return {
        isValid: value && value.length >= 2,
        suggestion:
          value && value.length < 2
            ? 'Please enter at least 2 characters'
            : undefined,
        confidence: 0.8,
      };
    }

    return { isValid: true, confidence: 0.5 };
  }

  // OCR processing
  async processOCR(
    file: File,
  ): Promise<{
    success: boolean;
    extractedText?: string;
    confidence?: number;
    detectedFields?: Record<string, string>;
    error?: string;
  }> {
    await this.simulateNetworkDelay(2000); // Longer delay for OCR processing

    // Simulate success/failure based on file name or random chance
    const shouldSucceed = Math.random() > 0.2; // 80% success rate

    if (shouldSucceed) {
      return {
        ...mockOCRResponses.success,
        extractedText: `Extracted from ${file.name}:\n${mockOCRResponses.success.extractedText}`,
      };
    } else {
      return mockOCRResponses.failure;
    }
  }

  // Analytics
  async sendAnalyticsEvent(event: any): Promise<void> {
    // Simulate network delay but don't block
    setTimeout(() => {
      addMockAnalyticsEvent(event);
    }, 100);
  }

  // WebSocket simulation
  createMockWebSocket(sessionId: string): MockWebSocket {
    return new MockWebSocket(sessionId, this);
  }

  // Private helper methods
  private async simulateNetworkDelay(ms: number = 300): Promise<void> {
    return new Promise((resolve) => {
      setTimeout(resolve, ms + Math.random() * 200);
    });
  }

  private validateFormField(
    fieldType: string,
    value: any,
  ): { isValid: boolean; message?: string } {
    switch (fieldType) {
      case 'email':
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return {
          isValid: emailRegex.test(value),
          message: emailRegex.test(value)
            ? undefined
            : 'Please enter a valid email address',
        };

      case 'text':
      case 'textarea':
        return {
          isValid: value && value.trim().length > 0,
          message:
            value && value.trim().length > 0
              ? undefined
              : 'This field is required',
        };

      case 'number':
        const num = Number(value);
        return {
          isValid: !isNaN(num),
          message: !isNaN(num) ? undefined : 'Please enter a valid number',
        };

      case 'select':
        return {
          isValid: value && value.trim().length > 0,
          message:
            value && value.trim().length > 0
              ? undefined
              : 'Please select an option',
        };

      case 'checkbox':
        return { isValid: true }; // Checkboxes are always valid

      case 'file':
        return { isValid: true }; // Files are validated separately

      default:
        return { isValid: true };
    }
  }
}

// Mock WebSocket implementation
class MockWebSocket {
  private sessionId: string;
  private apiService: MockApiService;
  private listeners: Map<string, Function[]> = new Map();
  private isConnected: boolean = false;

  constructor(sessionId: string, apiService: MockApiService) {
    this.sessionId = sessionId;
    this.apiService = apiService;

    // Simulate connection delay
    setTimeout(() => {
      this.isConnected = true;
      this.emit('open', {});
    }, 500);
  }

  addEventListener(event: string, callback: Function): void {
    if (!this.listeners.has(event)) {
      this.listeners.set(event, []);
    }
    this.listeners.get(event)!.push(callback);
  }

  removeEventListener(event: string, callback: Function): void {
    const callbacks = this.listeners.get(event);
    if (callbacks) {
      const index = callbacks.indexOf(callback);
      if (index > -1) {
        callbacks.splice(index, 1);
      }
    }
  }

  send(data: string): void {
    if (!this.isConnected) {
      console.warn('WebSocket not connected');
      return;
    }

    try {
      const message = JSON.parse(data);
      this.handleMessage(message);
    } catch (error) {
      console.error('Invalid WebSocket message:', error);
    }
  }

  close(): void {
    this.isConnected = false;
    this.emit('close', {});
  }

  private handleMessage(message: any): void {
    switch (message.type) {
      case 'send_message':
        // Echo back a response after delay
        setTimeout(() => {
          this.emit('message', {
            data: JSON.stringify({
              type: 'message',
              content: `Echo: ${message.content}`,
              timestamp: new Date().toISOString(),
            }),
          });
        }, 1000);
        break;

      case 'start_typing':
        // Simulate typing indicator
        setTimeout(() => {
          this.emit('message', {
            data: JSON.stringify({
              type: 'typing_start',
            }),
          });
        }, 100);
        break;

      case 'stop_typing':
        this.emit('message', {
          data: JSON.stringify({
            type: 'typing_stop',
          }),
        });
        break;

      case 'ping':
        this.emit('message', {
          data: JSON.stringify({
            type: 'pong',
          }),
        });
        break;
    }
  }

  private emit(event: string, data: any): void {
    const callbacks = this.listeners.get(event);
    if (callbacks) {
      callbacks.forEach((callback) => callback(data));
    }
  }
}

// Export singleton instance
export const mockApiService = new MockApiService();
export { MockWebSocket };
