import React from 'react';
import { useConfiguration } from '../hooks/useConfiguration';
import { useChat } from '../hooks/useChat';
import ChatButton from './ChatButton';
import ChatWindow from './ChatWindow';
import { WidgetProvider } from '../context/WidgetContext';

interface ChatWidgetProps {
  chatbotId: string;
  apiUrl: string;
  container?: string;
  autoOpen?: boolean;
  theme?: 'auto' | 'light' | 'dark';
  language?: string;
  customCSS?: string;
  onReady?: () => void;
  onMessage?: (message: any) => void;
  onError?: (error: Error) => void;
}

const ChatWidget: React.FC<ChatWidgetProps> = ({
  chatbotId,
  apiUrl,
  container,
  autoOpen = false,
  theme = 'auto',
  language = 'en',
  customCSS,
  onReady,
  onMessage,
  onError,
}) => {
  const { config, loading, error } = useConfiguration({ chatbotId, apiUrl });
  const chat = useChat(config);

  // Handle auto-open
  React.useEffect(() => {
    if (config && autoOpen && !chat.state.isOpen) {
      chat.actions.toggleChat();
    }
  }, [config, autoOpen, chat.state.isOpen, chat.actions]);

  // Call onReady when widget is initialized
  React.useEffect(() => {
    if (config && onReady) {
      onReady();
    }
  }, [config, onReady]);

  // Handle errors
  React.useEffect(() => {
    if (error && onError) {
      onError(new Error(error));
    }
  }, [error, onError]);

  // Handle message events
  React.useEffect(() => {
    if (chat.state.session?.messages.length && onMessage) {
      const lastMessage =
        chat.state.session.messages[chat.state.session.messages.length - 1];
      if (lastMessage.type === 'bot') {
        onMessage(lastMessage);
      }
    }
  }, [chat.state.session?.messages, onMessage]);

  // Apply custom CSS
  React.useEffect(() => {
    if (customCSS) {
      const link = document.createElement('link');
      link.rel = 'stylesheet';
      link.href = customCSS;
      document.head.appendChild(link);

      return () => {
        document.head.removeChild(link);
      };
    }
  }, [customCSS]);

  // Apply theme
  const effectiveTheme = React.useMemo(() => {
    if (theme === 'auto') {
      return window.matchMedia('(prefers-color-scheme: dark)').matches
        ? 'dark'
        : 'light';
    }
    return theme;
  }, [theme]);

  const themeConfig = React.useMemo(() => {
    if (!config) return null;

    return {
      ...config.theme,
      mode: effectiveTheme,
    };
  }, [config, effectiveTheme]);

  if (loading) {
    return null; // Or loading spinner
  }

  if (error || !config) {
    console.error('ChatWidget error:', error);
    return null;
  }

  const widgetStyle: React.CSSProperties = {
    '--primary-color': themeConfig?.primaryColor,
    '--secondary-color': themeConfig?.secondaryColor,
    '--background-color': themeConfig?.backgroundColor,
    '--bubble-color': themeConfig?.bubbleColor,
    '--text-color': themeConfig?.textColor,
  } as React.CSSProperties;

  return (
    <WidgetProvider
      value={{ config: { ...config, theme: themeConfig! }, language }}>
      <div
        className={`chatbot-widget ${
          themeConfig?.mode === 'dark' ? 'dark' : ''
        }`}
        style={widgetStyle}
        data-chatbot-id={chatbotId}
        data-theme={effectiveTheme}>
        <ChatButton
          isOpen={chat.state.isOpen}
          unreadCount={chat.state.unreadCount}
          onClick={chat.actions.toggleChat}
          config={config}
        />

        {chat.state.isOpen && (
          <ChatWindow
            state={chat.state}
            actions={chat.actions}
            config={config}
          />
        )}
      </div>
    </WidgetProvider>
  );
};

export default ChatWidget;
