import React from 'react';
import { MessageCircle, X, Minimize2 } from 'lucide-react';
import { ChatbotConfig } from '../types/widget.types';

interface ChatButtonProps {
  isOpen: boolean;
  unreadCount: number;
  onClick: () => void;
  config: ChatbotConfig;
}

const ChatButton: React.FC<ChatButtonProps> = ({
  isOpen,
  unreadCount,
  onClick,
  config,
}) => {
  const sizeClasses = {
    small: 'w-12 h-12',
    medium: 'w-14 h-14',
    large: 'w-16 h-16',
  };

  const positionClasses = {
    'bottom-right': 'bottom-4 right-4',
    'bottom-left': 'bottom-4 left-4',
  };

  const iconSizes = {
    small: 20,
    medium: 24,
    large: 28,
  };

  return (
    <button
      onClick={onClick}
      className={`
        fixed ${positionClasses[config.position]} ${sizeClasses[config.size]}
        bg-primary-500 hover:bg-primary-600 text-white
        rounded-full shadow-lg hover:shadow-xl
        transition-all duration-300 ease-in-out
        focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2
        z-[9999]
        ${!isOpen && unreadCount > 0 ? 'animate-pulse' : ''}
      `}
      style={{
        backgroundColor: config.theme.primaryColor,
      }}
      aria-label={isOpen ? 'Close chat' : 'Open chat'}
      aria-expanded={isOpen}
    >
      {/* Icon */}
      <div className="flex items-center justify-center w-full h-full">
        {isOpen ? (
          <X size={iconSizes[config.size]} />
        ) : (
          <MessageCircle size={iconSizes[config.size]} />
        )}
      </div>

      {/* Unread badge */}
      {!isOpen && unreadCount > 0 && (
        <div
          className="absolute -top-1 -right-1 min-w-[20px] h-5 px-1
                     text-xs font-bold text-white rounded-full
                     flex items-center justify-center
                     animate-bounce"
          style={{
            backgroundColor: config.theme.secondaryColor,
          }}
        >
          {unreadCount > 99 ? '99+' : unreadCount}
        </div>
      )}

      {/* Pulse effect for new messages */}
      {!isOpen && unreadCount > 0 && (
        <div
          className="absolute inset-0 rounded-full animate-ping opacity-20"
          style={{
            backgroundColor: config.theme.primaryColor,
          }}
        />
      )}
    </button>
  );
};

export default ChatButton;