import { useState, useEffect, useCallback } from 'react';
import { ChatbotConfig } from '../types/widget.types';
import { mockApiService } from '../services/mockApiService';

interface UseConfigurationProps {
  chatbotId: string;
  apiUrl: string;
}

export const useConfiguration = ({
  chatbotId,
  apiUrl,
}: UseConfigurationProps) => {
  const [config, setConfig] = useState<ChatbotConfig | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Default configuration fallback
  const getDefaultConfig = useCallback(
    (): ChatbotConfig => ({
      id: chatbotId,
      displayName: 'AI Assistant',
      welcomeMessage: 'Hello! How can I help you today?',
      avatar:
        'https://images.pexels.com/photos/8386440/pexels-photo-8386440.jpeg?auto=compress&cs=tinysrgb&w=64&h=64&dpr=2',
      theme: {
        primaryColor: '#3B82F6',
        secondaryColor: '#14B8A6',
        backgroundColor: '#FFFFFF',
        bubbleColor: '#F3F4F6',
        textColor: '#374151',
        mode: 'light',
      },
      position: 'bottom-right',
      size: 'medium',
      apiUrl,
      tenantId: 'default',
      features: {
        fileUpload: true,
        voiceInput: false,
        escalation: true,
        analytics: true,
      },
    }),
    [chatbotId, apiUrl],
  );

  // Load configuration from API
  const loadConfiguration = useCallback(async () => {
    setLoading(true);
    setError(null);

    try {
      // Check cache first
      const cacheKey = `chatbot_config_${chatbotId}`;
      const cachedConfig = localStorage.getItem(cacheKey);
      const cacheExpiry = localStorage.getItem(`${cacheKey}_expiry`);

      if (cachedConfig && cacheExpiry && Date.now() < parseInt(cacheExpiry)) {
        setConfig(JSON.parse(cachedConfig));
        setLoading(false);
        return;
      }

      // Fetch from mock API
      const configData: ChatbotConfig = await mockApiService.getChatbotConfig(
        chatbotId,
      );
      const fullConfig = { ...getDefaultConfig(), ...configData };

      setConfig(fullConfig);

      // Cache configuration for 5 minutes
      localStorage.setItem(cacheKey, JSON.stringify(fullConfig));
      localStorage.setItem(
        `${cacheKey}_expiry`,
        (Date.now() + 5 * 60 * 1000).toString(),
      );
    } catch (err) {
      console.error('Failed to load configuration:', err);
      setError(err instanceof Error ? err.message : 'Unknown error');

      // Use default configuration as fallback
      setConfig(getDefaultConfig());
    } finally {
      setLoading(false);
    }
  }, [chatbotId, apiUrl, getDefaultConfig]);

  // Update configuration (for live updates)
  const updateConfiguration = useCallback(
    (updates: Partial<ChatbotConfig>) => {
      setConfig((prev) => (prev ? { ...prev, ...updates } : null));

      // Update cache
      if (config) {
        const cacheKey = `chatbot_config_${chatbotId}`;
        const updatedConfig = { ...config, ...updates };
        localStorage.setItem(cacheKey, JSON.stringify(updatedConfig));
      }
    },
    [config, chatbotId],
  );

  // Refresh configuration
  const refreshConfiguration = useCallback(() => {
    // Clear cache
    const cacheKey = `chatbot_config_${chatbotId}`;
    localStorage.removeItem(cacheKey);
    localStorage.removeItem(`${cacheKey}_expiry`);

    // Reload
    loadConfiguration();
  }, [chatbotId, loadConfiguration]);

  // Load configuration on mount
  useEffect(() => {
    loadConfiguration();
  }, [loadConfiguration]);

  // Listen for configuration updates via window messages (for live preview)
  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      if (
        event.data.type === 'CHATBOT_CONFIG_UPDATE' &&
        event.data.chatbotId === chatbotId
      ) {
        updateConfiguration(event.data.config);
      }
    };

    window.addEventListener('message', handleMessage);
    return () => window.removeEventListener('message', handleMessage);
  }, [chatbotId, updateConfiguration]);

  return {
    config,
    loading,
    error,
    refreshConfiguration,
    updateConfiguration,
  };
};
