import React from 'react';
import { Bot, User, Clock, CheckCircle } from 'lucide-react';
import { Message, ChatbotConfig, QuickReply } from '../types/widget.types';

interface MessageBubbleProps {
  message: Message;
  config: ChatbotConfig;
  onQuickReply?: (value: any) => void;
}

const MessageBubble: React.FC<MessageBubbleProps> = ({
  message,
  config,
  onQuickReply,
}) => {
  const isUser = message.type === 'user';
  const isSystem = message.type === 'system';
  
  const formatTime = (date: Date) => {
    return new Intl.DateTimeFormat('en-US', {
      hour: '2-digit',
      minute: '2-digit',
    }).format(date);
  };

  if (isSystem) {
    return (
      <div className="flex justify-center">
        <div className="text-xs text-gray-500 dark:text-gray-400 bg-gray-100 dark:bg-gray-700 px-3 py-1 rounded-full">
          {message.content}
        </div>
      </div>
    );
  }

  return (
    <div className={`flex ${isUser ? 'justify-end' : 'justify-start'} items-end space-x-2`}>
      {!isUser && (
        <div className="flex-shrink-0 w-8 h-8 rounded-full overflow-hidden">
          <img
            src={config.avatar}
            alt={config.displayName}
            className="w-full h-full object-cover"
          />
        </div>
      )}
      
      <div className={`flex flex-col ${isUser ? 'items-end' : 'items-start'} max-w-xs`}>
        {/* Message bubble */}
        <div
          className={`
            px-4 py-2 rounded-2xl shadow-sm
            ${isUser 
              ? 'rounded-br-md text-white' 
              : 'rounded-bl-md text-gray-800 dark:text-gray-200'
            }
          `}
          style={{
            backgroundColor: isUser 
              ? config.theme.primaryColor 
              : config.theme.bubbleColor,
            color: isUser 
              ? 'white' 
              : config.theme.textColor,
          }}
        >
          {/* Message content */}
          <div className="text-sm leading-relaxed whitespace-pre-wrap">
            {message.content}
          </div>
          
          {/* File attachments */}
          {message.metadata?.files && (
            <div className="mt-2 space-y-1">
              {message.metadata.files.map((file: any, index: number) => (
                <div
                  key={index}
                  className="flex items-center space-x-2 p-2 bg-white/10 rounded"
                >
                  <div className="text-xs">📎 {file.name}</div>
                </div>
              ))}
            </div>
          )}
        </div>
        
        {/* Quick replies */}
        {message.metadata?.quickReplies && onQuickReply && (
          <div className="mt-2 flex flex-wrap gap-2">
            {message.metadata.quickReplies.map((reply: QuickReply, index: number) => (
              <button
                key={index}
                onClick={() => onQuickReply(reply.value)}
                className="px-3 py-1 text-xs rounded-full border transition-colors
                         hover:bg-opacity-10 hover:bg-gray-800"
                style={{
                  borderColor: config.theme.primaryColor,
                  color: config.theme.primaryColor,
                }}
              >
                {reply.icon && <span className="mr-1">{reply.icon}</span>}
                {reply.text}
              </button>
            ))}
          </div>
        )}
        
        {/* Timestamp */}
        <div className="flex items-center space-x-1 mt-1">
          <span className="text-xs text-gray-400 dark:text-gray-500">
            {formatTime(message.timestamp)}
          </span>
          
          {isUser && (
            <CheckCircle 
              size={12} 
              className="text-green-500" 
              aria-label="Message delivered"
            />
          )}
        </div>
      </div>
      
      {isUser && (
        <div className="flex-shrink-0 w-8 h-8 rounded-full bg-gray-200 dark:bg-gray-600 flex items-center justify-center">
          <User size={16} className="text-gray-600 dark:text-gray-300" />
        </div>
      )}
    </div>
  );
};

export default MessageBubble;