import React, { useState, useEffect } from 'react';
import { Send, Upload, X, Check } from 'lucide-react';
import { ConversationalForm, FormField, ChatbotConfig, AIForm } from '../types/widget.types';
import { validateField, processOCR, smartValidate, getFieldExample, getConfirmationMessage } from '../utils/formValidation';
import { sendAnalyticsEvent } from '../utils/analytics';

interface FormRendererProps {
  formContext: ConversationalForm;
  config: ChatbotConfig;
  onFieldComplete: (fieldId: string, value: any) => void;
  onFormComplete: (data: Record<string, any>) => void;
  onFormCancel: () => void;
}

const FormRenderer: React.FC<FormRendererProps> = ({
  formContext,
  config,
  onFieldComplete,
  onFormComplete,
  onFormCancel,
}) => {
  const [currentValue, setCurrentValue] = useState<any>('');
  const [validationError, setValidationError] = useState<string>('');
  const [isProcessing, setIsProcessing] = useState(false);
  const [showSummary, setShowSummary] = useState(false);

  // Get current field
  const getCurrentField = (): FormField | null => {
    if (!formContext.form?.fields) return null;
    return formContext.form.fields[formContext.currentFieldIndex] || null;
  };

  const currentField = getCurrentField();

  // Reset current value when field changes
  useEffect(() => {
    setCurrentValue('');
    setValidationError('');
  }, [formContext.currentFieldIndex]);

  // Check if form is complete
  useEffect(() => {
    if (formContext.isComplete) {
      setShowSummary(true);
    }
  }, [formContext.isComplete]);

  const handleFieldSubmit = async () => {
    if (!currentField) return;

    setIsProcessing(true);
    setValidationError('');

    try {
      // Basic validation
      const basicValidation = validateField(currentField, currentValue);
      if (!basicValidation.isValid) {
        setValidationError(basicValidation.message);
        setIsProcessing(false);
        return;
      }

      // AI-enhanced validation if enabled
      if (currentField.aiFeatures?.includes('smart-validation')) {
        const smartValidation = await smartValidate(
          currentField,
          currentValue,
          formContext.collectedData
        );
        
        if (!smartValidation.isValid) {
          setValidationError(smartValidation.suggestion || 'Please check your input');
          setIsProcessing(false);
          return;
        }
      }

      // Field completed successfully
      onFieldComplete(currentField.id, currentValue);
      
      // Track field completion
      sendAnalyticsEvent({
        type: 'message_sent',
        sessionId: 'current-session',
        chatbotId: config.id,
        tenantId: config.tenantId,
        metadata: {
          fieldId: currentField.id,
          fieldType: currentField.type,
          formId: formContext.formId,
        },
      });

    } catch (error) {
      console.error('Form field validation error:', error);
      setValidationError('An error occurred. Please try again.');
    } finally {
      setIsProcessing(false);
    }
  };

  const handleFileUpload = async (files: FileList) => {
    if (!currentField || !files.length) return;

    setIsProcessing(true);
    
    try {
      const file = files[0];
      
      // OCR processing if enabled
      if (currentField.aiFeatures?.includes('ocr')) {
        const ocrResult = await processOCR(file);
        if (ocrResult.success) {
          setCurrentValue(ocrResult.extractedText);
        } else {
          setValidationError('Could not process the document. Please try again.');
        }
      } else {
        // Regular file handling
        setCurrentValue(file.name);
      }
    } catch (error) {
      console.error('File upload error:', error);
      setValidationError('File upload failed. Please try again.');
    } finally {
      setIsProcessing(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleFieldSubmit();
    }
  };

  const renderFieldInput = () => {
    if (!currentField) return null;

    switch (currentField.type) {
      case 'select':
        return (
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">
              {currentField.label} {currentField.required && <span className="text-red-500">*</span>}
            </label>
            <div className="grid grid-cols-1 gap-2">
              {currentField.options?.map((option, index) => (
                <button
                  key={index}
                  onClick={() => setCurrentValue(option)}
                  className={`
                    p-3 text-left rounded-lg border transition-all
                    ${currentValue === option
                      ? 'border-blue-500 bg-blue-50 dark:bg-blue-900/20'
                      : 'border-gray-300 dark:border-gray-600 hover:border-gray-400'
                    }
                  `}
                >
                  {option}
                </button>
              ))}
            </div>
          </div>
        );

      case 'checkbox':
        return (
          <div className="space-y-2">
            <label className="flex items-center space-x-3 cursor-pointer">
              <input
                type="checkbox"
                checked={currentValue}
                onChange={(e) => setCurrentValue(e.target.checked)}
                className="w-4 h-4 text-blue-600 rounded focus:ring-blue-500"
              />
              <span className="text-sm text-gray-700 dark:text-gray-300">
                {currentField.label} {currentField.required && <span className="text-red-500">*</span>}
              </span>
            </label>
          </div>
        );

      case 'file':
        return (
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">
              {currentField.label} {currentField.required && <span className="text-red-500">*</span>}
            </label>
            <div className="border-2 border-dashed border-gray-300 dark:border-gray-600 rounded-lg p-6 text-center">
              <Upload className="mx-auto h-8 w-8 text-gray-400 mb-2" />
              <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                {currentField.aiFeatures?.includes('ocr')
                  ? 'Upload a document - I can extract text automatically'
                  : 'Click to upload or drag and drop'
                }
              </p>
              <input
                type="file"
                onChange={(e) => e.target.files && handleFileUpload(e.target.files)}
                className="hidden"
                id="file-upload"
                accept=".jpg,.jpeg,.png,.pdf,.doc,.docx"
              />
              <label
                htmlFor="file-upload"
                className="cursor-pointer inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700"
              >
                Choose File
              </label>
              {currentValue && (
                <p className="mt-2 text-sm text-green-600">
                  Selected: {currentValue}
                </p>
              )}
            </div>
          </div>
        );

      case 'textarea':
        return (
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">
              {currentField.label} {currentField.required && <span className="text-red-500">*</span>}
            </label>
            <textarea
              value={currentValue}
              onChange={(e) => setCurrentValue(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder={currentField.placeholder}
              rows={3}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md
                       focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent
                       bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                       placeholder-gray-500 dark:placeholder-gray-400"
            />
          </div>
        );

      default:
        return (
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">
              {currentField.label} {currentField.required && <span className="text-red-500">*</span>}
            </label>
            <input
              type={currentField.type}
              value={currentValue}
              onChange={(e) => setCurrentValue(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder={currentField.placeholder}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md
                       focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent
                       bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                       placeholder-gray-500 dark:placeholder-gray-400"
            />
          </div>
        );
    }
  };

  const renderFormSummary = () => {
    return (
      <div className="space-y-4">
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
          Form Summary
        </h3>
        
        <div className="space-y-3">
          {Object.entries(formContext.collectedData).map(([fieldId, value]) => {
            const field = formContext.form?.fields.find(f => f.id === fieldId);
            if (!field) return null;
            
            return (
              <div key={fieldId} className="flex justify-between items-center py-2 border-b border-gray-200 dark:border-gray-700">
                <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
                  {field.label}:
                </span>
                <span className="text-sm text-gray-900 dark:text-white">
                  {typeof value === 'boolean' ? (value ? 'Yes' : 'No') : value}
                </span>
              </div>
            );
          })}
        </div>
        
        <div className="flex space-x-3">
          <button
            onClick={() => onFormComplete(formContext.collectedData)}
            className="flex-1 bg-green-600 text-white px-4 py-2 rounded-md hover:bg-green-700 transition-colors"
          >
            <Check className="inline w-4 h-4 mr-2" />
            Submit
          </button>
          
          <button
            onClick={() => setShowSummary(false)}
            className="flex-1 bg-gray-600 text-white px-4 py-2 rounded-md hover:bg-gray-700 transition-colors"
          >
            Edit
          </button>
          
          <button
            onClick={onFormCancel}
            className="px-4 py-2 border border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 rounded-md hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors"
          >
            <X className="inline w-4 h-4" />
          </button>
        </div>
      </div>
    );
  };

  if (!formContext.form) {
    return null;
  }

  if (showSummary) {
    return (
      <div className="p-4 bg-white dark:bg-gray-800 border-t border-gray-200 dark:border-gray-700">
        {renderFormSummary()}
      </div>
    );
  }

  if (!currentField) {
    return null;
  }

  return (
    <div className="p-4 bg-white dark:bg-gray-800 border-t border-gray-200 dark:border-gray-700">
      <div className="space-y-4">
        {/* Progress indicator */}
        <div className="flex items-center justify-between text-sm text-gray-600 dark:text-gray-400">
          <span>
            Step {formContext.currentFieldIndex + 1} of {formContext.form.fields.length}
          </span>
          <div className="flex space-x-1">
            {formContext.form.fields.map((_, index) => (
              <div
                key={index}
                className={`w-2 h-2 rounded-full ${
                  index <= formContext.currentFieldIndex
                    ? 'bg-blue-500'
                    : 'bg-gray-300 dark:bg-gray-600'
                }`}
              />
            ))}
          </div>
        </div>

        {/* Field input */}
        {renderFieldInput()}

        {/* Validation error */}
        {validationError && (
          <div className="text-sm text-red-600 dark:text-red-400">
            {validationError}
          </div>
        )}

        {/* Action buttons */}
        <div className="flex space-x-3">
          <button
            onClick={handleFieldSubmit}
            disabled={isProcessing || !currentValue}
            className="flex-1 bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 
                     disabled:opacity-50 disabled:cursor-not-allowed transition-colors
                     flex items-center justify-center"
          >
            {isProcessing ? (
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
            ) : (
              <>
                <Send className="w-4 h-4 mr-2" />
                {formContext.currentFieldIndex === formContext.form.fields.length - 1 ? 'Complete' : 'Next'}
              </>
            )}
          </button>
          
          <button
            onClick={onFormCancel}
            className="px-4 py-2 border border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 
                     rounded-md hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors"
          >
            Cancel
          </button>
        </div>
      </div>
    </div>
  );
};

export default FormRenderer;