// Utility functions for mock data
export const getRandomResponse = (
  category: keyof typeof mockResponses,
): string => {
  const responses = mockResponses[category] || mockResponses.default;
  return responses[Math.floor(Math.random() * responses.length)];
};

export const generateSessionId = (): string => {
  return `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
};

export const simulateTypingDelay = (): Promise<void> => {
  return new Promise((resolve) => {
    setTimeout(resolve, 1000 + Math.random() * 2000); // 1-3 seconds
  });
};

// Mock API responses
export const mockApiResponses = {
  createSession: (chatbotId: string) => ({
    id: generateSessionId(),
    chatbotId,
    userId: '',
    messages: [],
    status: 'active' as 'active',
    metadata: {
      userAgent: navigator.userAgent,
      referrer: document.referrer,
      startTime: new Date(),
      lastActivity: new Date(),
    },
  }),

  sendMessage: (sessionId: string, content: string) => {
    // Simulate AI response based on message content
    let responseCategory = 'default';
    const lowerContent = content.toLowerCase();

    if (
      lowerContent.includes('hello') ||
      lowerContent.includes('hi') ||
      lowerContent.includes('hey')
    ) {
      responseCategory = 'greeting';
    } else if (
      lowerContent.includes('help') ||
      lowerContent.includes('assist')
    ) {
      responseCategory = 'help';
    } else if (
      lowerContent.includes('form') ||
      lowerContent.includes('fill') ||
      lowerContent.includes('submit')
    ) {
      responseCategory = 'form';
    } else if (
      lowerContent.includes('human') ||
      lowerContent.includes('agent') ||
      lowerContent.includes('person')
    ) {
      responseCategory = 'escalation';
    } else if (
      lowerContent.includes('bye') ||
      lowerContent.includes('goodbye') ||
      lowerContent.includes('thanks')
    ) {
      responseCategory = 'goodbye';
    }

    return {
      id: Date.now().toString(),
      type: 'bot' as const,
      content: getRandomResponse(responseCategory),
      timestamp: new Date(),
      metadata: {
        quickReplies:
          responseCategory === 'greeting'
            ? mockQuickReplies.slice(0, 3)
            : undefined,
      },
    };
  },
};

