import { FormField, ValidationRule } from '../types/widget.types';

interface ValidationResult {
  isValid: boolean;
  message: string;
}

interface SmartValidationResult {
  isValid: boolean;
  suggestion?: string;
  confidence: number;
}

interface OCRResult {
  success: boolean;
  extractedText?: string;
  confidence?: number;
  detectedFields?: Record<string, string>;
  error?: string;
}

// Basic field validation
export const validateField = (field: FormField, value: any): ValidationResult => {
  // Required field validation
  if (field.required && (!value || (typeof value === 'string' && value.trim() === ''))) {
    return {
      isValid: false,
      message: `${field.label} is required`,
    };
  }

  // Type-specific validation
  switch (field.type) {
    case 'email':
      if (value && !isValidEmail(value)) {
        return {
          isValid: false,
          message: 'Please enter a valid email address',
        };
      }
      break;

    case 'number':
      if (value && isNaN(Number(value))) {
        return {
          isValid: false,
          message: 'Please enter a valid number',
        };
      }
      break;

    case 'text':
    case 'textarea':
      if (value && typeof value === 'string') {
        // Check minimum length
        const minLength = field.validation?.find(rule => rule.type === 'minLength')?.value;
        if (minLength && value.length < minLength) {
          return {
            isValid: false,
            message: `Minimum ${minLength} characters required`,
          };
        }

        // Check maximum length
        const maxLength = field.validation?.find(rule => rule.type === 'maxLength')?.value;
        if (maxLength && value.length > maxLength) {
          return {
            isValid: false,
            message: `Maximum ${maxLength} characters allowed`,
          };
        }

        // Check pattern
        const pattern = field.validation?.find(rule => rule.type === 'pattern')?.value;
        if (pattern && !new RegExp(pattern).test(value)) {
          return {
            isValid: false,
            message: field.validation?.find(rule => rule.type === 'pattern')?.message || 'Invalid format',
          };
        }
      }
      break;

    case 'select':
      if (value && field.options && !field.options.includes(value)) {
        return {
          isValid: false,
          message: 'Please select a valid option',
        };
      }
      break;

    case 'date':
      if (value && !isValidDate(value)) {
        return {
          isValid: false,
          message: 'Please enter a valid date',
        };
      }
      break;
  }

  return {
    isValid: true,
    message: '',
  };
};

// Email validation
const isValidEmail = (email: string): boolean => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
};

// Date validation
const isValidDate = (date: string): boolean => {
  const parsedDate = new Date(date);
  return !isNaN(parsedDate.getTime());
};

// AI-enhanced smart validation
export const smartValidate = async (
  field: FormField,
  value: any,
  context: Record<string, any>
): Promise<SmartValidationResult> => {
  try {
    // Use mock validation service
    const { mockApiService } = await import('../services/mockApiService');
    return await mockApiService.smartValidateField(field.type, value);

  } catch (error) {
    console.error('Smart validation error:', error);
    // Fallback to basic validation
    const basicResult = validateField(field, value);
    return {
      isValid: basicResult.isValid,
      suggestion: basicResult.message,
      confidence: 0.5,
    };
  }
};

// OCR processing for file fields
export const processOCR = async (file: File): Promise<OCRResult> => {
  try {
    // Check file type
    const allowedTypes = ['image/jpeg', 'image/png', 'image/gif', 'application/pdf'];
    if (!allowedTypes.includes(file.type)) {
      return {
        success: false,
        error: 'Unsupported file type. Please upload an image or PDF.',
      };
    }

    // Check file size (10MB limit)
    if (file.size > 10 * 1024 * 1024) {
      return {
        success: false,
        error: 'File too large. Please upload a file smaller than 10MB.',
      };
    }

    // Use mock OCR service
    const { mockApiService } = await import('../services/mockApiService');
    return await mockApiService.processOCR(file);

  } catch (error) {
    console.error('OCR processing error:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'OCR processing failed',
    };
  }
};

// Face verification for identity fields
export const verifyFace = async (photo: Blob, idDocument: File): Promise<{
  isMatch: boolean;
  confidence: number;
  reason?: string;
}> => {
  try {
    // Mock face verification - simulate processing
    await new Promise(resolve => setTimeout(resolve, 2000));
    
    // Simulate 85% success rate
    const isMatch = Math.random() > 0.15;
    
    return {
      isMatch,
      confidence: isMatch ? 0.92 : 0.45,
      reason: isMatch ? undefined : 'Face does not match the document photo'
    };

  } catch (error) {
    console.error('Face verification error:', error);
    return {
      isMatch: false,
      confidence: 0,
      reason: 'Verification service unavailable',
    };
  }
};

// Generate field examples for help
export const getFieldExample = (field: FormField): string => {
  switch (field.type) {
    case 'email':
      return 'example@domain.com';
    case 'number':
      return '123';
    case 'date':
      return '2024-01-15';
    case 'text':
      if (field.label.toLowerCase().includes('name')) {
        return 'John Doe';
      } else if (field.label.toLowerCase().includes('phone')) {
        return '+1 (555) 123-4567';
      }
      return 'Example text';
    case 'textarea':
      return 'Please provide detailed information here...';
    default:
      return '';
  }
};

// Get validation error messages
export const getValidationMessage = (field: FormField, value: any): string => {
  const validation = validateField(field, value);
  return validation.message;
};

// Get confirmation messages
export const getConfirmationMessage = (field: FormField, value: any): string => {
  switch (field.type) {
    case 'email':
      return `Perfect! I'll use ${value} as your email address.`;
    case 'number':
      return `Got it! I have ${value}.`;
    case 'select':
      return `Excellent choice! I've noted "${value}".`;
    case 'checkbox':
      return value ? 'Understood!' : 'Noted.';
    default:
      return `Thank you! I have "${value}".`;
  }
};

// Suggest field corrections
export const suggestCorrection = async (fieldType: string, invalidValue: any): Promise<string[]> => {
  const suggestions: string[] = [];

  switch (fieldType) {
    case 'email':
      // Common email typos
      const emailValue = invalidValue.toString().toLowerCase();
      if (emailValue.includes('gmai')) {
        suggestions.push(emailValue.replace('gmai', 'gmail'));
      }
      if (emailValue.includes('yahooo')) {
        suggestions.push(emailValue.replace('yahooo', 'yahoo'));
      }
      if (emailValue.includes('.cm')) {
        suggestions.push(emailValue.replace('.cm', '.com'));
      }
      break;

    case 'number':
      // Extract numbers from text
      const numbers = invalidValue.toString().match(/\d+/g);
      if (numbers) {
        suggestions.push(numbers.join(''));
      }
      break;

    case 'date':
      // Try to parse common date formats
      const dateStr = invalidValue.toString();
      const dateFormats = [
        /(\d{1,2})\/(\d{1,2})\/(\d{4})/,  // MM/DD/YYYY
        /(\d{1,2})-(\d{1,2})-(\d{4})/,   // MM-DD-YYYY
        /(\d{4})\/(\d{1,2})\/(\d{1,2})/,  // YYYY/MM/DD
      ];

      for (const format of dateFormats) {
        const match = dateStr.match(format);
        if (match) {
          const [, part1, part2, part3] = match;
          suggestions.push(`${part3}-${part2.padStart(2, '0')}-${part1.padStart(2, '0')}`);
          break;
        }
      }
      break;
  }

  return suggestions;
};