import React, { useRef, useEffect, useState } from 'react';
import { X, Minimize2, Send, Paperclip, User, Phone } from 'lucide-react';
import { WidgetState, ChatbotConfig, Message } from '../types/widget.types';
import MessageBubble from './MessageBubble';
import TypingIndicator from './TypingIndicator';
import FormRenderer from './FormRenderer';
import { sendAnalyticsEvent } from '../utils/analytics';

interface ChatWindowProps {
  state: WidgetState;
  actions: {
    toggleChat: () => void;
    minimizeChat: () => void;
    maximizeChat: () => void;
    addUserMessage: (message: string) => Promise<void>;
    escalateToAgent: () => Promise<void>;
    startTyping: () => void;
    stopTyping: () => void;
  };
  config: ChatbotConfig;
}

const ChatWindow: React.FC<ChatWindowProps> = ({ state, actions, config }) => {
  const [inputValue, setInputValue] = useState('');
  const [isInputFocused, setIsInputFocused] = useState(false);
  const [isWaitingForResponse, setIsWaitingForResponse] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const sizeClasses = {
    small: 'w-80 h-96',
    medium: 'w-96 h-[500px]',
    large: 'w-[420px] h-[600px]',
  };

  const positionClasses = {
    'bottom-right': 'bottom-20 right-4',
    'bottom-left': 'bottom-20 left-4',
  };

  // Auto-scroll to bottom
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [state.session?.messages]);

  // Focus input when window opens
  useEffect(() => {
    if (state.isOpen && inputRef.current) {
      inputRef.current.focus();
    }
  }, [state.isOpen]);

  const handleSendMessage = async () => {
    if (!inputValue.trim() || !state.session) return;

    const message = inputValue.trim();
    setInputValue('');
    setIsWaitingForResponse(true);
    
    // Stop typing indicator
    actions.stopTyping();
    
    try {
      await actions.addUserMessage(message);
    } finally {
      setIsWaitingForResponse(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(e.target.value);
    
    // Start typing indicator
    if (e.target.value && !state.isTyping) {
      actions.startTyping();
    } else if (!e.target.value && state.isTyping) {
      actions.stopTyping();
    }
  };

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      // Handle file upload logic
      const file = files[0];
      actions.addUserMessage(`📎 Uploaded: ${file.name}`);
    }
  };

  const handleQuickReply = async (value: any) => {
    setIsWaitingForResponse(true);
    try {
      await actions.addUserMessage(typeof value === 'string' ? value : JSON.stringify(value));
    } finally {
      setIsWaitingForResponse(false);
    }
  };

  // Check if current message has quick replies
  const lastBotMessage = state.session?.messages
    .filter(m => m.type === 'bot')
    .pop();
  const hasQuickReplies = lastBotMessage?.metadata?.quickReplies?.length > 0;

  return (
    <div
      className={`
        fixed ${positionClasses[config.position]} ${sizeClasses[config.size]}
        bg-white dark:bg-gray-800 rounded-lg shadow-2xl
        flex flex-col z-[9998]
        transition-all duration-300 ease-in-out
        ${state.isMinimized ? 'h-12' : ''}
        border border-gray-200 dark:border-gray-700
      `}
      style={{
        backgroundColor: config.theme.backgroundColor,
        borderColor: config.theme.mode === 'dark' ? '#374151' : '#E5E7EB',
      }}
    >
      {/* Header */}
      <div
        className="flex items-center justify-between p-4 border-b border-gray-200 dark:border-gray-700 rounded-t-lg"
        style={{
          backgroundColor: config.theme.primaryColor,
          color: 'white',
        }}
      >
        <div className="flex items-center space-x-3">
          <img
            src={config.avatar}
            alt={config.displayName}
            className="w-8 h-8 rounded-full object-cover"
          />
          <div>
            <h3 className="font-semibold text-sm">{config.displayName}</h3>
            <div className="flex items-center space-x-1">
              <div className="w-2 h-2 bg-green-400 rounded-full"></div>
              <span className="text-xs opacity-90">
                {state.connectionStatus === 'connected' ? 'Online' : 'Connecting...'}
              </span>
            </div>
          </div>
        </div>
        
        <div className="flex items-center space-x-2">
          {config.features.escalation && (
            <button
              onClick={actions.escalateToAgent}
              className="p-1 hover:bg-white/10 rounded transition-colors"
              aria-label="Talk to human agent"
              title="Talk to human agent"
            >
              <User size={16} />
            </button>
          )}
          
          <button
            onClick={actions.minimizeChat}
            className="p-1 hover:bg-white/10 rounded transition-colors"
            aria-label="Minimize chat"
          >
            <Minimize2 size={16} />
          </button>
          
          <button
            onClick={actions.toggleChat}
            className="p-1 hover:bg-white/10 rounded transition-colors"
            aria-label="Close chat"
          >
            <X size={16} />
          </button>
        </div>
      </div>

      {!state.isMinimized && (
        <>
          {/* Messages */}
          <div className="flex-1 overflow-y-auto p-4 space-y-4 min-h-0 max-h-[350px]">
            {state.session?.messages.map((message) => (
              <MessageBubble
                key={message.id}
                message={message}
                config={config}
                onQuickReply={handleQuickReply}
              />
            ))}
            
            {(state.isTyping || isWaitingForResponse) && <TypingIndicator config={config} />}
            
            <div ref={messagesEndRef} />
          </div>

          {/* Quick Replies */}
          {hasQuickReplies && (
            <div className="px-4 pb-2">
              <div className="flex flex-wrap gap-2">
                {lastBotMessage?.metadata?.quickReplies?.map((reply, index) => (
                  <button
                    key={index}
                    onClick={() => handleQuickReply(reply.value)}
                    className="px-3 py-1 text-sm rounded-full border border-gray-300 dark:border-gray-600
                             hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
                    style={{
                      borderColor: config.theme.primaryColor,
                      color: config.theme.primaryColor,
                    }}
                  >
                    {reply.text}
                  </button>
                ))}
              </div>
            </div>
          )}

          {/* Form Renderer */}
          {state.formContext && (
            <FormRenderer
              formContext={state.formContext}
              config={config}
              onFieldComplete={(fieldId, value) => {
                // Handle form field completion
                console.log('Field completed:', fieldId, value);
              }}
              onFormComplete={(data) => {
                // Handle form completion
                console.log('Form completed:', data);
              }}
              onFormCancel={() => {
                // Handle form cancellation
                console.log('Form cancelled');
              }}
            />
          )}

          {/* Input Area */}
          <div className="p-4 border-t border-gray-200 dark:border-gray-700">
            <div className="flex items-center space-x-2">
              {config.features.fileUpload && (
                <>
                  <button
                    onClick={() => fileInputRef.current?.click()}
                    className="p-2 text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200 transition-colors"
                    aria-label="Upload file"
                  >
                    <Paperclip size={18} />
                  </button>
                  <input
                    ref={fileInputRef}
                    type="file"
                    multiple
                    className="hidden"
                    onChange={handleFileUpload}
                    accept=".jpg,.jpeg,.png,.gif,.pdf,.doc,.docx,.txt"
                  />
                </>
              )}
              
              <div className="flex-1 relative">
                <input
                  ref={inputRef}
                  type="text"
                  value={inputValue}
                  onChange={handleInputChange}
                  onKeyPress={handleKeyPress}
                  onFocus={() => setIsInputFocused(true)}
                  onBlur={() => setIsInputFocused(false)}
                  placeholder="Type your message..."
                  className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-full
                           focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent
                           bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                           placeholder-gray-500 dark:placeholder-gray-400"
                  style={{
                    borderColor: isInputFocused ? config.theme.primaryColor : undefined,
                  }}
                />
              </div>
              
              <button
                onClick={handleSendMessage}
                disabled={!inputValue.trim() || isWaitingForResponse}
                className="p-2 rounded-full transition-all duration-200
                         disabled:opacity-50 disabled:cursor-not-allowed
                         enabled:hover:shadow-md enabled:active:scale-95"
                style={{
                  backgroundColor: config.theme.primaryColor,
                  color: 'white',
                }}
                aria-label="Send message"
              >
                {isWaitingForResponse ? (
                  <div className="animate-spin rounded-full h-[18px] w-[18px] border-2 border-white border-t-transparent"></div>
                ) : (
                  <Send size={18} />
                )}
              </button>
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default ChatWindow;