import * as React from 'react';
import { createRoot } from 'react-dom/client';
import ChatWidget from './components/ChatWidget';
import { analytics } from './utils/analytics';
import './widget.css';

// Widget initialization interface
interface WidgetInitOptions {
  chatbotId: string;
  apiUrl: string;
  container?: string;
  autoOpen?: boolean;
  theme?: 'auto' | 'light' | 'dark';
  language?: string;
  customCSS?: string;
  onReady?: () => void;
  onMessage?: (message: any) => void;
  onError?: (error: Error) => void;
}

// Global widget API
class ChatbotWidgetAPI {
  private initialized = false;
  private root: any = null;
  private container: HTMLElement | null = null;

  // Initialize widget
  init(options: WidgetInitOptions): void {
    if (this.initialized) {
      console.warn('ChatbotWidget is already initialized');
      return;
    }

    const { chatbotId, apiUrl, container = 'body', ...restOptions } = options;

    // Validate required options
    if (!chatbotId) {
      throw new Error('chatbotId is required');
    }
    if (!apiUrl) {
      throw new Error('apiUrl is required');
    }

    try {
      // Find or create container
      this.container =
        typeof container === 'string'
          ? document.querySelector(container)
          : container;

      if (!this.container) {
        // Create container if not found
        this.container = document.createElement('div');
        this.container.id = 'chatbot-widget-container';
        document.body.appendChild(this.container);
      }

      // Initialize analytics
      analytics.init(apiUrl, true);

      // Track widget load start
      const loadStartTime = performance.now();

      // Create React root and render widget
      this.root = createRoot(this.container);
      this.root.render(
        <ChatWidget
          chatbotId={chatbotId}
          apiUrl={apiUrl}
          {...restOptions}
          onReady={() => {
            // Track widget load completion
            const loadTime = performance.now() - loadStartTime;
            analytics.trackWidgetLoad(chatbotId, 'default', loadTime);

            if (restOptions.onReady) {
              restOptions.onReady();
            }
          }}
        />,
      );

      this.initialized = true;

      // Track page view
      analytics.trackPageView(chatbotId, 'default');
    } catch (error) {
      console.error('Failed to initialize ChatbotWidget:', error);
      if (restOptions.onError) {
        restOptions.onError(error as Error);
      }
    }
  }

  // Destroy widget
  destroy(): void {
    if (!this.initialized) {
      return;
    }

    if (this.root) {
      this.root.unmount();
      this.root = null;
    }

    if (this.container && this.container.id === 'chatbot-widget-container') {
      this.container.remove();
    }

    this.container = null;
    this.initialized = false;
  }

  // Check if widget is initialized
  isInitialized(): boolean {
    return this.initialized;
  }

  // Get widget version
  getVersion(): string {
    return '1.0.0';
  }

  // Send custom event
  sendEvent(eventType: string, data?: any): void {
    if (this.initialized) {
      window.dispatchEvent(
        new CustomEvent(`chatbot-widget-${eventType}`, {
          detail: data,
        }),
      );
    }
  }

  // Listen to widget events
  on(eventType: string, callback: (event: CustomEvent) => void): void {
    window.addEventListener(`chatbot-widget-${eventType}`, callback);
  }

  // Remove event listener
  off(eventType: string, callback: (event: CustomEvent) => void): void {
    window.removeEventListener(`chatbot-widget-${eventType}`, callback);
  }
}

// Create global instance
const widgetAPI = new ChatbotWidgetAPI();

// Auto-initialization from script attributes
const autoInit = () => {
  const scripts = document.querySelectorAll('script[data-chatbot-id]');
  const script = scripts[scripts.length - 1] as HTMLScriptElement;

  if (script) {
    const chatbotId = script.getAttribute('data-chatbot-id');
    const apiUrl = script.getAttribute('data-api-url');
    const theme = script.getAttribute('data-theme') as
      | 'auto'
      | 'light'
      | 'dark';
    const autoOpen = script.getAttribute('data-auto-open') === 'true';
    const language = script.getAttribute('data-language') || 'en';

    if (chatbotId && apiUrl) {
      // Initialize when DOM is ready
      if (document.readyState === 'loading') {
        document.addEventListener('DOMContentLoaded', () => {
          widgetAPI.init({
            chatbotId,
            apiUrl,
            theme,
            autoOpen,
            language,
          });
        });
      } else {
        widgetAPI.init({
          chatbotId,
          apiUrl,
          theme,
          autoOpen,
          language,
        });
      }
    }
  }
};

// Expose API to global scope
declare global {
  interface Window {
    ChatbotWidget: ChatbotWidgetAPI;
  }
}

window.ChatbotWidget = widgetAPI;

// Auto-initialize if loaded via script tag
autoInit();

// Export for module usage
export default widgetAPI;
export { ChatbotWidgetAPI };
export type { WidgetInitOptions };
